html {
  height:100%;
  font-size:15px;
}

.display_none {
  display:none;
}
body {
  height:100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: transparent;
}

.display_none {
  display:none;
}
.rows {
  display: flex;
}
#root {
  height:100%;
}
.all {
  height:100%;
}

.container-fluid {
  height:93.2%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.sidebar {
  height:100%;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}
#sidebar-wrapper{
  width: 100vw;
  margin-left: -1rem;
  min-width:20%;
  transition: margin .25s ease-out;
}
#sidebar-wrapper .sidebar-heading {
  padding: 0.875rem 1.25rem;
  font-size: 1.2rem;
}

#page-content-wrapper {
  min-width: 0;
  width: 100%;
  text-align:center;
}

.images {
  width: 600px;
  margin-top: 20px;
}

#nav {
    background-color: #3288c4;
    height:100%;
}

header {
    height: 4.8%;
}

#nav_content > a, #nav_content > div {
    float:left;
}

.navbar-brand svg {
    float:left;
    margin-top:6px;
}

.mr-auto {
    margin-left:10px;
}

#home_link {
    margin-top: 3px;
}
.main_body {
    height:91.2%;
    padding-top: 20px;
    margin-right: 10px;
    width: auto;
	min-width: 1900px;
}

.modal {
    display:    none;
    position:   fixed;
    z-index:    1000;
    top:        0;
    left:       0;
    height:     100%;
    width:      100%;
    background: rgba( 255, 255, 255, .8 )
    url('https://i.stack.imgur.com/FhHRx.gif')
    50% 50%
    no-repeat;
}
body.loading {
    overflow: hidden;
}
body.loading .modal {
    display: block;
}

#dZUpload {
    height: 94%;
    border : 1px solid skyblue;
}
.dropzone, .outerDropzone, .formfield {
    height:98%;
    width: 100%;
}
#upload_image {
    margin-top: 22%;
    width: 114px;
    height: 114px;
    color: skyblue;
    cursor: pointer;
}
#img_upload {
    width: 58.5%;
    height: 830px;
    margin-left: 20px;
    text-align: center;
    padding:20px;
	min-width: 1130px;
}
#jsCanvas{
    margin-top: 30px;
}

.dz-button {
    margin-top: 21%;
    width: 100%;
    height: 94%;
}


#my-dropzone {
    margin-top: 17px;
    margin-bottom: 4px;
    height: 97%;
    border: 4px dashed skyblue;
}

input[type="text"] {
    border: 1px solid #cccccc;
    margin: 0.6rem 0;
    padding: 0.2rem 0.4rem;
    background-color: darkgrey;
    border-radius: 3px;
}

canvas {
    background-color:black;
}
.well {
    min-height: 20px;
    padding: 43px;
    margin-bottom: 20px;
    background-color: #f5f5f5;
    border: 1px solid #e3e3e3;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
}

#toolbar {
    width:100%;
    height:100%;
}

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 15px;
    border-radius: 5px;  
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    transition: opacity .2s;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: #4CAF50;
    cursor: pointer;
  }
  
  .slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
  }
  
legend {
    display: block;
    width: 100%;
    padding: 0;
    margin-bottom: 20px;
    font-size: 21px;
    line-height: 40px;
    color: #333333;
    border: 0;
    border-bottom: 1px solid #e5e5e5;
}

.labels {
    justify-content:end;
}
.radio.inline {
    margin-right:10px;
}

.form-in {
    flex-flow: row wrap;
    align-items: center;
}
.btn:hover, .btn:focus {
    color: #333333;
    text-decoration: none;
    background-position: 0 -15px;
    transition: background-position 0.1s linear;
}
[class*="span"] {
    float: left;
    min-height: 1px;
    margin-left: 20px;
    width:19.3%;
}
.btn {
    margin-right: 7px;
    display: inline-block;
    *display: inline;
    padding: 4px 12px;
    margin-bottom: 0;
    *margin-left: .3em;
    font-size: 14px;
    line-height: 20px;
    color: #333333;
    text-align: center;
    text-shadow: 0 1px 1px rgba(255, 255, 255, 0.75);
    vertical-align: middle;
    cursor: pointer;
    background-color: #f5f5f5;
    *background-color: #e6e6e6;
    background-image: linear-gradient(to bottom, #ffffff, #e6e6e6);
    background-repeat: repeat-x;
    border: 1px solid #cccccc;
    *border: 0;
    border-color: #e6e6e6 #e6e6e6 #bfbfbf;
    border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
    border-bottom-color: #b3b3b3;
    border-radius: 4px;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffffff', endColorstr='#ffe6e6e6', GradientType=0);
    filter: progid:DXImageTransform.Microsoft.gradient(enabled=false);
    *zoom: 1;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
}

.control-group {
    margin-bottom: 10px;
}

